(function () {
  'use strict';

  angular.module('BlurAdmin.pages.buscador')
    .controller('buscadorProductDetailController', buscadorProductDetailController);

  buscadorProductDetailController.$inject = ['$log', '$timeout', '$uibModal', '$uibModalInstance', 'Restangular', 'localStorageService',
    'item', 'currentPosition', 'orderByDistance', 'useFerreterias'];
  function buscadorProductDetailController($log, $timeout, $uibModal, $uibModalInstance, Restangular, localStorageService,
    item, currentPosition, orderByDistance, useFerreterias) {
    var vm = this;
    
    vm.user = localStorageService.get('user');
    vm.item = item;
    vm.showCode = false;
    vm.isLoading = false;
    vm.products = [];
    vm.busqueda = {upc: item.UPC};
    vm.maxSize = 5;
    vm.itemsPerPage = 15;
    vm.total = 0;
    vm.page = 1;
    vm.hayProductos = false;

    $timeout(function() {
      JsBarcode('#barcode', vm.item.UPC);
    }, 100);

    vm.showCodeItems = function() {
      vm.showCode = !vm.showCode;

      var operation = vm.showCode ? 'removeClass' : 'addClass';
      $('.code-item')[operation]('visible-md visible-lg');
    };

    vm.buscar = function() {
      vm.isLoading = true;
      vm.busqueda.page = vm.page;
      vm.busqueda.limit = vm.itemsPerPage;

      if (vm.user && vm.user.privilegio !== 'administrador') {
        vm.busqueda.useFerreterias = useFerreterias;
        vm.busqueda.privilegio = vm.user.privilegio;
        vm.busqueda.ownerID = (vm.user['propietario'] || vm.user['vendedor']).id;
      }

      Restangular.all('productos_lista').customGET('', vm.busqueda).then(function(resp) {
        vm.products = (resp || {}).data || [];

        vm.adJustProducts();

        vm.total = (resp || {}).total || 0;
        vm.page = (resp || {}).current_page || 0;
        vm.hayProductos = vm.products.length > 0;
        vm.isLoading = false;
      }, function() {
        vm.isLoading = false;
      });
    };

    vm.openItemInfo = function(item) {
      var modalInstance = $uibModal.open({
        animation: true,
        templateUrl: 'app/pages/buscador/product-detail/product-detail-info.template.html',
        controller: 'buscadorProductDetailInfoController',
        controllerAs: 'vm',
        size: 'lg',
        resolve: {
          item: function() {
            return item;
          }
        }
      });
  
      modalInstance.result.then(function() {
        
      }, function () {
        $log.info('Modal dismissed at: ' + new Date());
      });
    };

    vm.closeDialog = function() {
      $uibModalInstance.dismiss();
    };

    vm.getDistance = function(lat1, lon1, lat2, lon2) {
      var R = 6371; // Radius of the earth in km
      var dLat = deg2rad(lat2 - lat1);  // deg2rad below
      var dLon = deg2rad(lon2 - lon1); 
      var a = 
        Math.sin(dLat / 2) * Math.sin(dLat / 2) +
        Math.cos(deg2rad(lat1)) * Math.cos(deg2rad(lat2)) * 
        Math.sin(dLon / 2) * Math.sin(dLon / 2); 
      var c = 2 * Math.atan2(Math.sqrt(a), Math.sqrt(1 - a)); 
      var d = R * c * 1000; // Distance in m
      
      return d;
    };
    
    function deg2rad(deg) {
      return deg * (Math.PI/180)
    };

    vm.adJustProducts = function() {
      var currentPosition = localStorageService.get('current-position');
      var lastUpdatePosition = moment(localStorageService.get('last-update-position'), 'MM/DD/YYYY HH:mm');
      var currentTime = moment(new Date(), 'MM/DD/YYYY HH:mm');
      var timeToUpdate = 5;

      var updateProductWithDistance = function(position) {
        vm.products.forEach(function(product, index) {
          if (product.aUbicacion) {
            var point = product.aUbicacion.substr(1).slice(0, -1).split(',')
            var lat = +point[0];
            var log = +point[1];

            var distance = +vm.getDistance(lat, log, position.coords.latitude, position.coords.longitude).toFixed(2);
            var labelDistance = distance > 500 ? (distance / 1000).toFixed(2) + ' Km' : distance + ' m';
            
            vm.products[index].showDistance = true;
            vm.products[index].distance = distance;
            vm.products[index].labelDistance = labelDistance;            
          } else {
            vm.products[index].distance = Infinity;
          }
        });

        if (orderByDistance) {
          vm.products = _.sortBy(vm.products, function(product) {
            return product.distance;
          });
        }
      };

      if (!_.isEmpty(currentPosition) && lastUpdatePosition.isValid(), currentTime.diff(lastUpdatePosition, 'minutes') < timeToUpdate) {
        updateProductWithDistance(currentPosition);
      } else {
        navigator.geolocation.getCurrentPosition(function(position) {
          updateProductWithDistance(position);
        }, function() {
          console.error('Error utilizando geolocalizacion para busqueda');
        }); 
      }
    };

    vm.buscar();
  }
})();

(function () {
  'use strict';

  angular.module('BlurAdmin.pages.login')
    .controller('LoginWizardController', LoginWizardController);

    LoginWizardController.$injec = ['localStorageService', '$rootScope', 'Restangular', 'toastr', '$state'];
  function LoginWizardController(localStorageService, $rootScope, Restangular, toastr, $state) {
    var vm = this;
    var mediumRegex = new RegExp("^(((?=.*[a-z])(?=.*[A-Z]))|((?=.*[a-z])(?=.*[0-9]))|((?=.*[A-Z])(?=.*[0-9])))(?=.{6,})")

    vm.personalInfo = {};
    vm.accountInfo = {};
    vm.user = localStorageService.get('user');
    vm.isPasswordValid = true;

    vm.loadPersonalInfo = function() {
      if (vm.user.privilegio === 'propietario') {
        vm.personalInfo.ci = vm.user.propietario.pCI;
        vm.personalInfo.nombre = vm.user.propietario.pNombre;
        vm.personalInfo.apellidos = vm.user.propietario.pApellidos;
        vm.personalInfo.direccion = vm.user.propietario.pDireccion;
        vm.personalInfo.telefono = vm.user.propietario.pTelefono;
      } else if (vm.user.privilegio === 'vendedor') {
        vm.personalInfo.ci = vm.user.vendedor.vCI;
        vm.personalInfo.nombre = vm.user.vendedor.vNombre;
        vm.personalInfo.apellidos = vm.user.vendedor.vApellidos;
        vm.personalInfo.direccion = vm.user.vendedor.vDireccion;
        vm.personalInfo.telefono = vm.user.vendedor.vTelefono;
      }
    };

    vm.loadAccountInfo = function() {
      vm.accountInfo.username = vm.user.name;
      vm.accountInfo.email = vm.user.email;
      vm.accountInfo.password = '';
      vm.accountInfo.confirmPassword = '';
    };

    vm.areAccountInfoPasswordsEqual = function () {
      return vm.accountInfo.confirmPassword && vm.accountInfo.password == vm.accountInfo.confirmPassword;
    };

    vm.validatePassword = function() {
      vm.isPasswordValid = mediumRegex.test(vm.accountInfo.password);
    };

    vm.redirect = function() {      
      if (vm.user.privilegio === 'administrador') {
        $state.go('dashboard.global');
      }
      if (vm.user.privilegio === 'vendedor') {
        $state.go('buscador');
      }
      if (vm.user.privilegio === 'propietario') {
        $state.go('dashboard.global');
      }
    };

    vm.updateInfo = function() {
      return function() {
        var endpoint = vm.user.privilegio === 'propietario' ? 'propietarios' : 'vendedores';
        var id = vm.user[vm.user.privilegio].id;
        var entity = {};

        if (vm.user.privilegio === 'propietario') {
          vm.user.propietario.pCI = vm.personalInfo.ci;
          vm.user.propietario.pNombre = vm.personalInfo.nombre;
          vm.user.propietario.pApellidos = vm.personalInfo.apellidos ;
          vm.user.propietario.pDireccion = vm.personalInfo.direccion;
          vm.user.propietario.pTelefono = vm.personalInfo.telefono;
        } else if (vm.user.privilegio === 'vendedor') {
          vm.user.vendedor.vCI = vm.personalInfo.ci;
          vm.user.vendedor.vNombre = vm.personalInfo.nombre;
          vm.user.vendedor.vApellidos = vm.personalInfo.apellidos;
          vm.user.vendedor.vDireccion = vm.personalInfo.direccion;
          vm.user.vendedor.vTelefono = vm.personalInfo.telefono;
        }

        entity = vm.user.privilegio === 'propietario' ? vm.user.propietario : vm.user.vendedor;
        $rootScope.$pageIsUpdating = true;
        
        Restangular.one(endpoint, id).customPUT(entity).then(function(resp) {
          vm.user.nuevo = false;
          vm.user.password = vm.accountInfo.password;
          
          Restangular.one('usuarios', vm.user.id).customPUT(vm.user).then(function(resp) {
            toastr.success('El perfil fue actualizado correctamente', 'Perfil');
            localStorageService.set('user', vm.user);
            $rootScope.$pageIsUpdating = false;

            vm.redirect();
          }, function() {
            $rootScope.$pageIsUpdating = false;
          })
        }, function() {
          toastr.error('Error actualizando cuenta');
          $rootScope.$pageIsUpdating = false;
        });
      }
    };

    vm.loadPersonalInfo();
    vm.loadAccountInfo();
  }
})();

(function () {
  'use strict';

  angular.module('BlurAdmin.pages.dashboard')
    .controller('dashboardFerreteriasController', dashboardFerreteriasController);

  dashboardFerreteriasController.$inject = ['$timeout', 'Restangular'];
  function dashboardFerreteriasController($timeout, Restangular) {
    var vm = this;
    var startMonth = moment().startOf('month');
    var endMonth = moment().endOf('month');

    vm.displayReport = false;
    vm.isLoading = false;
    vm.fromDate = new Date(+startMonth.format('YYYY'), +startMonth.format('MM') - 1, +startMonth.format('DD'));
    vm.toDate = new Date(+endMonth.format('YYYY'), +endMonth.format('MM') - 1, +endMonth.format('DD'));
    vm.format = 'dd-MM-yyyy';
    vm.options = {
      showWeeks: false
    };

    vm.load = function() {
      vm.isLoading = true;
      vm.displayReport = false;

      var busqueda = {
        from: moment(vm.fromDate).format('YYYY-MM-DD'),
        to: moment(vm.toDate).format('YYYY-MM-DD')
      };

      Restangular.all('almacenes_fecha').customGET('', busqueda).then(function(resp) {
        if (!_.isEmpty(resp)) {
          vm.isLoading = false;
          vm.displayReport = true;
          vm.loadReport(resp);
        } else {
          vm.isLoading = false;
          vm.displayReport = false;
        }
      });
    };

    vm.loadReport = function(dates) {
      var labels = dates.map(function(date) {
        return moment(date.fecha, 'YYYY-MM-DD').format('YYYY-MM-DD');
      });

      var data = dates.map(function(date) {
        return date.registros;
      });

      var config = {
        type: 'line',
        data: {
          labels: labels,
          datasets: [{
            label: 'Fechas de Registro',
            data: data,
            fill: true,
            backgroundColor: 'rgba(32,158,145, 0.6)',
          }]
        },
        options: {
          responsive: true,
          maintainAspectRatio: false,
          title:{
            display: false
          },
          tooltips: {
            mode: 'index',
            intersect: false,
          },
          hover: {
            mode: 'nearest',
            intersect: true
          },
          scales: {
            xAxes: [{
              display: true,
              scaleLabel: {
                display: true,
                labelString: 'Fecha'
              }
            }],
            yAxes: [{
              display: true,
              scaleLabel: {
                display: true,
                labelString: 'Registros'
              }
            }]
          }
        }
      };


      $timeout(function() {
        var ctx = document.getElementById('report').getContext('2d');
        window.myLine = new Chart(ctx, config);
      }, 100);
    };

    vm.load();
  }

})();

