(function() {
  'use strict';

  angular.module('BlurAdmin.pages.productos')
    .controller('productosItemController', productosItemController);

  productosItemController.$inject = ['$log', '$state', '$stateParams', '$rootScope', '$uibModal', 'serverAPI', 'Restangular', 'toastr'];
  function productosItemController($log, $state, $stateParams, $rootScope, $uibModal, serverAPI, Restangular, toastr) {
    $log.log('productosItemController');
    var vm = this;
    vm.producto = {};
    vm.ferreterias = [];
    vm.cantidades = [];
    vm.isValidUPC = true;

    vm.tabs = [{
      name: 'Detalles',
      state: 'productos_item.detail'
    }, {
      name: 'Categorias',
      state: 'productos_item.categories'
    }];

    vm.submit = function() {
      Restangular.one('productos', $stateParams.idProducto).customPUT(vm.producto).then(function(resp) {
        $state.go('categories_productos', $stateParams);
        $rootScope.$pageIsUpdating = false;

        toastr.success('Informacion actualizada', 'Producto');        
      }, function() {
        $rootScope.$pageIsUpdating = false;
        toastr.error('Error al actualizar el producto', 'Error');
      });
    };

    vm.upload = function(isValid) {
      if (!isValid || !vm.isValidUPC) {
        return;
      }

      $rootScope.$pageIsUpdating = true;
      if (vm.file && !vm.file.$error) {
        if (_.isObject(vm.file) && !vm.file.$error) {
          var timestamp = Number(new Date());
          var storageRef = firebase.storage().ref(timestamp.toString());
          var uploadTask = storageRef.put(vm.file);

          uploadTask.on(firebase.storage.TaskEvent.STATE_CHANGED,
            function(snapshot) {
              var progress = (snapshot.bytesTransferred / snapshot.totalBytes) * 100;
              console.log('Upload is ' + progress + '% done');
              switch (snapshot.state) {
                case firebase.storage.TaskState.PAUSED:
                  console.log('Upload is paused');
                  break;
                case firebase.storage.TaskState.RUNNING:
                  console.log('Upload is running');
                  break;
              }
            }, function(error) {
              $log.error(error.code);
            }, function() {
              vm.producto.pImagen = uploadTask.snapshot.downloadURL;
              vm.submit();
            });
        } else {
          vm.submit();
        }
      } else {
        vm.submit();
      }
    };

    vm.remove = function () {
      var deleteMessage = $uibModal.open({
        animation: true,
        templateUrl: 'app/pages/productos/borrar/borrar.template.html',
        controller: 'productosBorrarController',
        controllerAs: 'vm',
        resolve: {
          entity: function() {
            return {
              title: 'Producto',
              name: vm.producto.pNombre
            }
          }
        }
      });

      deleteMessage.result.then(function() {
        Restangular.one('productos', vm.producto.id).remove().then(function() {
          $state.go('categories_productos', $stateParams);
          toastr.success('Producto ha sido eliminado', 'Producto');
        }, function() {
          toastr.error('Error al eliminar el producto', 'Error');
        });
      }, function() {
        $log.log('Borrar fue cancelado')
      });
    };

    vm.load = function() {
      Restangular.one('productos', $stateParams.idProducto).get().then(function(resp) {
        vm.producto = resp;
      });

      Restangular.all('all-proveedores').getList().then(function(resp) {
        vm.proveedores = resp || [];
      });
    };

    vm.openUPC = function() {
      var upcModal = $uibModal.open({
        animation: true,
        templateUrl: 'app/pages/upc/buscador/buscador.template.html',
        controller: 'UPCBuscadorController',
        controllerAs: 'vm',
        resolve: {}
      });

      upcModal.result.then(function(upc) {
       vm.producto.UPC = upc.id;
       vm.isValidUPC = true;
      }, function() {
        $log.log('Borrar fue cancelado')
      });
    };

    vm.checkUPC = function() {
      Restangular.all('upc').customGET('', { id: vm.producto.UPC })
        .then(function(resp) {
          vm.isValidUPC = !_.isEmpty((resp || {}).data || []);
        });
    };

    vm.load();
  }
})();

(function() {
  'use strict';

  angular.module('BlurAdmin.pages.productos')
    .controller('productosListaController', productosListaController);

  productosListaController.$inject = ['$log', '$state', '$stateParams', '$timeout',
    '$uibModal', '$rootScope', 'serverAPI', 'Restangular', 'localStorageService'];
  function productosListaController($log, $state, $stateParams, $timeout, $uibModal, $rootScope,
                                    serverAPI, Restangular, localStorageService) {
    $log.log('productosListaController');
    var vm = this;
    vm.user = localStorageService.get('user') || {};
    vm.productos = [];
    vm.busqueda = {nombre: ''};
    vm.categoryList = [];

    vm.goTo = function(id) {
      $state.go('productos_item.detail', {id: $stateParams.id, idProducto: id});
    };

    vm.goToProducts = function(node) {
      $state.go('categories_productos', {id: $stateParams.id, idCategoria: node.id});
    };

    vm.addNewCategory = function(isCategory, parent) {
      var modalInstance = $uibModal.open({
        animation: true,
        templateUrl: 'app/pages/productos/nueva-categoria/nueva-categoria.template.html',
        controller: 'nuevaCategoriaController',
        controllerAs: 'vm',
        resolve: {
          isCategory: function() {
            return isCategory;
          },
          parent: function() {
            return parent;
          },
          entity: function() {
            return null;
          }
        }
      });
  
      modalInstance.result.then(function (selectedItem) {
        location.reload(true);
      }, function () {
        $log.info('Modal dismissed at: ' + new Date());
      });
    };

    vm.editCategory = function(isCategory, parent, node) {
      var modalInstance = $uibModal.open({
        animation: true,
        templateUrl: 'app/pages/productos/nueva-categoria/nueva-categoria.template.html',
        controller: 'nuevaCategoriaController',
        controllerAs: 'vm',
        resolve: {
          isCategory: function() {
            return isCategory;
          },
          parent: function() {
            return parent;
          },
          entity: function() {
            return node;
          }
        }
      });
  
      modalInstance.result.then(function (editedItem) {
        var indexItem = _.findIndex(vm.categories, {id: editedItem.id});

        if (indexItem >= 0) {
          _.extend(vm.categories[indexItem], editedItem);
          vm.loadGrid(vm.categories);
        }
      }, function () {
        $log.info('Modal dismissed at: ' + new Date());
      });
    };

    vm.deleteCategory = function(isCategory, node) {
      var modalInstance = $uibModal.open({
        animation: true,
        templateUrl: 'app/pages/productos/borrar/borrar.template.html',
        controller: 'productosBorrarController',
        controllerAs: 'vm',
        resolve: {
          entity: function() {
            return {
              title: isCategory ? 'Categoria' : 'Sub-Categoria',
              name: node.cNombre
            }
          }
        }
      });
  
      modalInstance.result.then(function () {
        Restangular.one(isCategory ? 'categorias' : 'subcategorias', node.id).remove().then(function() {
          location.reload(true);
        });
      }, function () {
        $log.info('Modal dismissed at: ' + new Date());
      });
    };

    vm.loadGrid = function(categories, isFromFilter) {
      vm.categoryList = categories
        .filter(function(category) {
          return !category.ParentId;
        }).map(function(category) {
          category.subcategories = categories.filter(function(subcategory) {
            return subcategory.ParentId === category.id;
          });
          return category;
        });

      if (isFromFilter) {
        vm.categoryList = vm.categoryList.filter(function(category) {
          return (category.subcategories || []).length > 0;
        });

        $timeout(function() {
          vm.grid.masonry('destroy');
          vm.grid = $('.category-list').masonry({
            itemSelector: '.category-item',
          });
          // vm.grid.masonry('reloadItems');
        }, 200);
      };
    };

    vm.load = function() {
      vm.isLoading = true;
      Restangular.one('almacenes', $stateParams.id).customGET('categorias').then(function(data) {
        vm.categories = data || [];
        vm.isLoading = false;
        vm.loadGrid(vm.categories);

        $timeout(function() {
          vm.grid = $('.category-list').masonry({
            itemSelector: '.category-item',
          });
        }, 200);
      }, function() {
        vm.isLoading = false;
      });
    };

    vm.buscar = function() {
      var filteredCategories = vm.categories.filter(function(category) {
        vm.busqueda.nombre = vm.busqueda.nombre || '';

        return !category.ParentId || category.cNombre.toLowerCase().indexOf(vm.busqueda.nombre.toLowerCase()) >= 0;
      });

      vm.loadGrid(filteredCategories, true);
    };

    vm.load();
  }
})();

(function() {
  'use strict';

  angular.module('BlurAdmin.pages.productos')
    .controller('listaProductosByCategoriesController', listaProductosByCategoriesController);

  listaProductosByCategoriesController.$inject = ['$state', '$stateParams', '$uibModal', 'Restangular', 'localStorageService'];
  function listaProductosByCategoriesController($state, $stateParams, $uibModal, Restangular, localStorageService) {
    var vm = this;
    vm.user = localStorageService.get('user') || {};
    vm.busqueda = {nombre: '', categoria: $stateParams.idCategoria};
    vm.isLoading = true;

    vm.addProduct = function () {
      $state.go('productos_registrar', {id: $stateParams.id, idCategoria: $stateParams.idCategoria});
    };

    vm.remove = function ($index, id) {
      var deleteMessage = $uibModal.open({
        animation: true,
        templateUrl: 'app/pages/productos/borrar/borrar.template.html',
        controller: 'productosBorrarController',
        controllerAs: 'vm',
        resolve: {
          entity: function() {
            return {
              title: 'Producto',
              name: vm.productos[$index].pNombre
            }
          }
        }
      });

      deleteMessage.result.then(function() {
        Restangular.one('productos', id).remove().then(function() {
          vm.productos.splice($index, 1);
        });
      }, function() {
        $log.log('Borrar fue cancelado')
      });
    };

    vm.buscar = function () {
      vm.isLoading = true;
      var id = vm.user.privilegio === 'vendedor' ? vm.user.vendedor.idAlmacen : $stateParams.id;
      Restangular.one('almacenes', id).customGET('showProductos', vm.busqueda).then(function (resp) {
        vm.categoria = (resp || {}).categoria;
        vm.productos = ((resp || {}).productos || []).map(function(item) {
          return item;
        });

        vm.isLoading = false;
      }, function() {
        vm.isLoading = false;
      });
    };

    vm.goTo = function(id) {
      $state.go('productos_item', {
        id: $stateParams.id,
        idCategoria: $stateParams.idCategoria,
        idProducto: id
      });
    }

    vm.goBackCategories = function() {
      $state.go('categories', {
        id: $stateParams.id
      }); 
    }

    vm.buscar();
  }
})();

(function() {
  'use strict';

  angular.module('BlurAdmin.pages.productos')
    .controller('nuevaCategoriaController', nuevaCategoriaController);

  nuevaCategoriaController.$inject = ['$rootScope', '$stateParams', '$uibModalInstance', 'Restangular', 'isCategory', 'parent', 'entity'];
  function nuevaCategoriaController($rootScope, $stateParams, $uibModalInstance, Restangular, isCategory, parent, oldEntity) {
    var vm = this;

    vm.isCategory = isCategory;
    vm.parent = parent;
    vm.label = isCategory ? 'categoria' : 'sub-categoria';
    vm.isOld = oldEntity !== null;
    vm.entity = {
      name: oldEntity ? oldEntity.cNombre || oldEntity.scNombre : '',
      image: oldEntity ? oldEntity.cImagen : '/assets/pictures/empty.png'
    };

    console.log(oldEntity)
    vm.submit = function() {
      var endpoint = vm.isCategory ? 'categorias' : 'subcategorias';
      var newEntity = {
        id: oldEntity ? oldEntity.id : shortid.gen(),
        idAlmacen: $stateParams.id
      };

      if (vm.isCategory) {
        newEntity.cNombre = vm.entity.name;
        newEntity.cImagen = vm.entity.image; 
      } else {
        newEntity.idCategoria = vm.parent.id;
        newEntity.cNombre = vm.entity.name;
        newEntity.scNombre = vm.entity.name;
        newEntity.qtyProductos = oldEntity ? oldEntity.qtyProductos || 0 : 0;
      }

      if (oldEntity) {
        Restangular.one(endpoint, oldEntity.id).customPUT(newEntity).then(function(resp) {
          $uibModalInstance.close(newEntity);
          $rootScope.$pageIsUpdating = false;
        }, function() {
          $rootScope.$pageIsUpdating = false;
        })
      } else {
        Restangular.all(endpoint).post(newEntity).then(function(resp) {
          $uibModalInstance.close(resp);
          $rootScope.$pageIsUpdating = false;
        }, function() {
          $rootScope.$pageIsUpdating = false;
        });
      }
    };

    vm.upload = function(isValid) {
      if (!isValid) {
        return;
      }

      $rootScope.$pageIsUpdating = true;

      if (vm.file && !vm.file.$error) {
        var timestamp = Number(new Date());
        var storageRef = firebase.storage().ref(timestamp.toString());
        var uploadTask = storageRef.put(vm.file);

        uploadTask.on(firebase.storage.TaskEvent.STATE_CHANGED,
          function(snapshot) {
            var progress = (snapshot.bytesTransferred / snapshot.totalBytes) * 100;
            console.log('Upload is ' + progress + '% done');
            switch (snapshot.state) {
              case firebase.storage.TaskState.PAUSED:
                console.log('Upload is paused');
                break;
              case firebase.storage.TaskState.RUNNING:
                console.log('Upload is running');
                break;
            }
          }, function(error) {
            $log.error(error.code);
          }, function() {
            vm.entity.image = uploadTask.snapshot.downloadURL;
            vm.submit();
          });
      } else {
        vm.submit();
      }
    };
  }
})();

